.avatar-image {
    border-radius: 50%;
    width: 68px;
    height: 68px;
    transition: .5s;

    &:hover {
         transform: scale(1.1);   
    }
}

.avatar__wrapper {
    overflow: hidden;
    border-radius: 50%;
    width: 68px;
    height: 68px;
}

.avatar__block {
    display: flex;
    align-items: center;
    gap: 15px;

    h2, p {
        margin: 0;
    }

    p {
        color: rgb(84, 84, 84);
    }
}

.testimonial__item {
    padding: 46px;
    width: 45%;
    background-color: #F1F1F1;
}

.testimonial__head {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 20px;
    border-bottom: 1px solid rgb(205, 205, 205);
    padding-bottom: 20px;
}

.testimonial__body {
    line-height: 142%;
    color: rgb(84, 84, 84);
}

.testimonial__list {
    position: relative;
    display: flex;
    gap: 30px 5%;
    flex-wrap: wrap;
}