.why {
    padding: 120px 0 240px;
    background-color: #DCEDFE;
}

.why__wrapper {
    margin: 0 auto;
    max-width: 922px;
}

.why-description {
    font-weight: 400;
    font-size: 16px;
    line-height: 152%;
    color: rgb(84, 84, 84);
}

.why-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    margin: 40px 0 0;
    padding: 0;
    font-weight: 600;
    font-size: 16px;
    line-height: 142%;
    color: var(--blue-color);
    gap: calc(32px + 19px + 9px) 46px;

    li {
        position: relative;

        &::before {
            content: url('../../img/tick.png');
            position: absolute;
            left: -20px;
            top: 50%;
            width: 19px;
            height: 19px;
            transform: translate(-50%, -55%);

        }
    }
}