// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Lato", sans-serif;
  --content-width: 1199px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --blue-light-color: #DCEDFE;
  --blue-color: #0152A8;
  --blue-dark-color: #036;
  --green-color: #0FE3AF;
  --green-dark-color: #0ebf93;
  --grey-dark-color: #144650;
  --green-light-color: #E5F2EF;
  --light-color: #F1F1F1;
  --footer-block: #E5F2EF;

}
