.about {
    height: 100vh;
    background-image: url('../../img/about_background.png');
    background-position: left;
    background-size: cover;
    background-repeat: no-repeat;

    .container {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        height: 100%;
    }
}

.about-wrapper {
    padding: 52px 40px;
    max-width: calc(924.25px - 80px);
    background-color: #FFF;

    h1 {
        margin-left: 30px;
        max-width: 430px;
        transition: .5s;

        &:hover {
            letter-spacing: 3px;
        }
    }

    p {
        max-width: 450px;
        line-height: 24px;
    }

}

.about-messages {
    display: flex;
    align-items: center;
    margin-top: 30px;
    gap: 30px;
}

.about-message {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    max-width: 200px;
    font-size: 12px;
    gap: 20px;

    .about-message-img {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        border: 1px solid #0FE3AF;
        border-radius: 50%;
        padding: 17px;

        img {
            z-index: 2;
            scale: (1.2);
        }

        &:hover {
            &::after {
                transform: scaleY(1);
            }
        }

        &::after {
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            z-index: 1;
            width: 100%;
            height: 100%;
            background-color: #0FE3AF;
            transform: scaleY(0);
            transform-origin: bottom;
            transition: .5s;
        }
    }

}