.footer {
    padding: 95px 0 0;
    background-color: var(--footer-block);
}

.footer-list {
    ul {
        display: flex;
        flex-direction: column;
        list-style: none;
        margin: 0;
        padding: 0;
        color: #545454;
        gap: 15px;

        li {
            a {
                position: relative;

                &::after {
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: -2px;
                    width: 100%;
                    height: 2px;
                    background-color: var(--blue-dark-color);
                    transform: scaleX(0);
                    transform-origin: left;
                    transition: .5s;
                }

                &:hover {
                    &::after {
                        transform: scaleX(1);
                    }
                }
            }
        }
    }
}

.footer__main {
    padding-bottom: 45px;

    .container {
        display: flex;
        justify-content: space-between;
        gap: 50px;

        .footer-title {
            font-size: 30px;
            line-height: 141%;
            color: rgb(28, 28, 28);
        }

        .footer-cell {
            flex: 2;

            &.footer-list {
                flex: 1;
            }

            &.footer-end {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 16px;

                h3 {
                    margin-bottom: 0;
                }

                p {
                    position: relative;
                    margin: 0;
                    line-height: 142%;
                    color: rgb(84, 84, 84);
                }

                input {
                    outline: none;
                    padding: 17px 25px 17px 5px;
                    width: 100%;
                }
            }
        }
    }
}

.footer__bottom {
    padding: 20px 0;
    text-align: center;
    background-color: white;
}

.footer__top {
    .container {
        display: flex;
        align-items: center; 
        justify-content: space-between;
        padding-bottom: 27px;
    }

    .contacts {
        display: flex;
        gap: 17px;
    }
}