.flip-card {
    width: 280px;
    height: 280px;
    background-color: transparent;
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transform-style: preserve-3d;
    transition: transform 0.6s;
  }
  
  .flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
  }
  
  .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
  .flip-card-front {
    padding: 30px 30px 45px;
    color: black;
    background: rgb(241, 241, 241);
  }
  
  .flip-card-back {
    color: white;
    background-color: #2980b9;
    transform: rotateY(180deg);
  }

  .card-title {
    font-weight: 600;
    font-size: 20px;
    color: rgb(28, 28, 28);
  }

  .card-paragraph {
    font-size: 14px;
    line-height: 142%;
    color: rgb(84, 84, 84);
  }

  .card-photo {
    width: 100%;
    height: 100%;
    image-orientation: flip;
  }

  .services-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;
  }