// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";
@import "./components/hero";
@import './components/services';
@import './components/about';
@import './components/partners';
@import './components/cta';
@import './components/testimonial';
@import './components/why';
@import './components/blog';
@import './components/footer';

a {
  color: inherit;
}

.block {
  margin-top: 150px;
}

.btn {
  border: 0;
  padding: 18px 37px;
  transition: .5s;
  cursor: pointer;
}

.btn-green {
  position: relative;
  font-size: 700;
  background-color: var(--green-color);

  &:hover {
    background-color: var(--green-dark-color);
  }
}

.btn-blue {
  position: relative;
  overflow: hidden;
  font-size: 700;
  color: rgb(255,255,255);
  background-color: var(--blue-color);
  transition: .3s ease;

  &:focus {
    background-color: var(--blue-dark-color);
  }

  &:hover {
    background-color: var(--blue-dark-color);
  }

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    left: var(--mouse-x);
    top: 50%;
    top: var(--mouse-y);
    border-radius: 100%;
    width: 0;
    height: 0;
    background: rgba(white,.1);
    transform: translate3d(-50%,-50%,0);
    transform-style: flat;
    transition: width .6s ease, height .6s ease;
  }

  &:active {
    &::after {
      width: 300px;
      height: 300px;
    }
  }

}

.btn-transparent {
  position: relative;
  border: 1.5px solid rgb(223, 223, 223);
  color: var(--blue-color);
  background-color: transparent;
  transition: color .3s ease;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-color: var(--blue-color);
    transform: scaleY(0);
    transform-origin: bottom;
    transition: 0.3s ease-in-out;
  }

  &:focus {
    color: rgb(255, 255, 255);

    &::before {
      transform: scaleX(1);
    }
  }

  &:hover {
    color: rgb(255, 255, 255);

    &::before {
      transform: scaleX(1);
    }
  }

  &:active {

  }

}

.message-green {
  padding: 0 3px;
  width: fit-content;
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  background-color: var(--green-color);
  transition: 0.3s linear;

  &:hover {
    -webkit-text-stroke: 1px rgb(249, 249, 249);
    color: rgb(249, 249, 249);
    background-color: var(--green-dark-color);
  }
}

.subtitle-section {
  position: relative;
  z-index: 3;
  border: 0.5px solid rgb(206, 206, 206);
  padding: 6px 18px;
  width: fit-content;
  font-weight: 400;
  font-size: 14px;
  letter-spacing: 5px;
  text-transform: uppercase;
  transition: .3s ease-in-out;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgb(206, 206, 206);
    transform: scaleY(0);
    transform-origin: bottom;
    transition: .3s ease-in-out;
  }

  &:hover {
    color: rgb(255, 255, 255);

    &::before {
      transform: scaleY(1);
    }
  }
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.title-section {
  font-weight: 600;
  font-size: 30px;

  &.center {
    text-align: center;
  }
}

.a-tel, .a-mail {
  position: relative;
  transition: .5s;

  &::before {
    position: absolute;
    left: -40px;
    top: -35%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 32.33px;
    height: 32.33px;
    background-color: var(--blue-dark-color);
  }
}

.a-tel {
  &::before {
    content: url('../../img/phone.png');
  }
}

.a-mail {
  &::before {
    content: url('../../img/gmail.png');
  }
}