.hero {
    min-height: 80vh;
    background-image: url('../../img/hero_background.png');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    .container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 15px;
        height: 80vh;
        max-width: 1200px;
    }
}

.hero-title {
    margin-bottom: 23px;
    font-weight: 700;
    font-size: 50px;
    color: var(--green-dark-color);
}

.hero-description {
    margin-top: 0;
    margin-bottom: 30px;
    max-width: 430px;
    font-weight: 400;
    font-size: 16px;
    color: rgb(28, 28, 28);
}