.partners {
    margin-top: 80px;
}

.partners_wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    row-gap: 50px;
}

.parther-image {
    transition: .3s;

    &:hover {
        filter: drop-shadow(0 6px rgba(0, 0, 0, 0.15));
        transform: scale(1.1);
    }
}