.blog-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.blog-list {
    display: flex;
    gap: 31px 20px;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 20px;
}

.blog-item a {
    display: flex;
    align-items: center;
    gap: 30px;

    .blog-image-wrapper {
        overflow: hidden;
        max-width: 220px;
        max-height: 147px;

        .blog-image {
            transition: .5s;

            &:hover {
                transform: scale(1.1);
            }
        }
    }
}

.blog-item__content {
    max-width: 335px;
}