.header__top {
    color: white;
    background-color: var(--blue-color);

    .container {
        display: flex;
        align-items: center; 
        justify-content: space-between;
        padding: 17px 0;
    }
}

.header__top-title {
    margin: 0;
    transition: .3s;

    &:hover {
        color: var(--green-color);
    }
}

.header__top_links {
    display: flex;
    gap: 100px;
}

.header__main {
    padding: 20px 0 30px;

    .container {
        display: flex;
        align-items: center; 
        justify-content: space-between;
    }
}

nav {
    ul {
        display: flex;
        list-style: none;
        gap: 40px;

        li {
            a {
                font-size: 16px;
                text-decoration: none;
                color: black;

                &:hover {
                    color: var(--blue-color);
                }
            }
        }
    }
}

.nav-link {
    position: relative;

    &.active {
        color: var(--blue-color);

        &::before {
            content: "";
            position: absolute;
            left: 0;
            bottom: -47px;
            width: 100%;
            height: 2px;
            background-color: var(--blue-color);
            transform: scaleX(1);
            transition: transform 0.3s ease-in-out;
        }
    }

    &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -47px;
        width: 100%;
        height: 2px;
        background-color: var(--blue-color);
        transform: scaleX(0);
        transition: transform 0.3s ease-in-out;
    }

    &:hover {
        &::before {
            transform: scaleX(1);
        }
    }
}