.cta {
    position: relative;
    overflow: hidden;
    background-color: var(--blue-color);

    .cta-bottom {
        position: absolute;
        left: -12%;
        bottom: -35%;
    }

    .cta-top {
        position: absolute;
        right: -12%;
        top: -35%;
    }
}

.cta__wrapper {
    padding: 125px 0;
    text-align: center;

    h2 {
        font-weight: 600;
        font-size: 30px;
        line-height: 130%;
        color: white;
    }
}